import { createRoot } from "react-dom/client";
import {
  Button,
  CssBase,
  ResponsiveSection,
  Text,
  Carousel,
} from '@stitch-fix/mode-react';
import ProductCard from '@stitch-fix/mode-react/ProductCard';


// Based on example here https://github.com/stitchfix/web-frontend/blob/main/packages/mode-react/stories/Carousel/Carousel.stories.tsx#L451-L463
function App() {
  if (window?.openai?.toolOutput) {
    console.log("tool output:", window.openai.toolOutput);
    // TODO: parse tool output and set products
  }

  const defaultProduct = {
    sku: 3333567,
    brand: "Hawker Rye",
    color: "Blue",
    pdpUrl: "https://www.stitchfix.com/product/Hawker-Rye-Hawker-Rye-Essential-Wash-Stretch/8PBQ72GDP?sku-id=3333567",
    name: "Hawker Rye Essential Wash Stretch",
    price: "$49.00",
    size: "M",
    imageUrlBase: "https://res-3.cloudinary.com/stitch-fix/image/upload/",
    imageUrlPath: "/v1723790242/ctv03lqixesfotnbowlj.jpg",
  }
  const products = Array(8).fill(defaultProduct);

  return (
    <>
      <CssBase />
      <Carousel
        ariaLabel="Controls Offset Top"
        slideWidth={{ sm: '220px' }}
        controlsOffsetTop={{
          sm: `${270 / 2}px`,
        }}
      >
        {products.map((product, index) => (
            <ProductCard
              key={index}
              heroMedia={{
                alt: '',
                baseUrl: product.imageUrlBase,
                mediaPath: product.imageUrlPath,
                sizes: { sm: 300 },
              }}
              brand={product.brand}
              colorName={product.color}
              href={product.pdpUrl}
              inseam={product?.inseam}
              name={product.name}
              price={product.price}
              size={product.size}
              sku={product.sku}
              cardSize="small"
              maxWidth="200px"
            />
        ))}
      </Carousel>
      <br></br>
      <div>
          <div>tool output:</div>
          <div>"{JSON.stringify(window?.openai?.toolOutput)}"</div>
      </div>
    </>
  );

}

createRoot(document.getElementById("stitch-fix-carousel-root")).render(<App />);
